import vue from 'vue'
import { bus } from '../main';

export default vue.extend({
  name: 'Nav',
  created(){
    bus.$on("ChangeLan", this.Lan);
  },
  destroyed(){
    bus.$off("ChangeLan", this.Lan);
  },
  methods: {
    changeLan(tag){
      console.log("Change to: " + tag);
      bus.$emit("ChangeLan", tag);
    },
    changeTopic(v){
      bus.$emit("ChangeTopic", v);
    },
    Lan(tag){
      this.lan = tag;
    }
  },
  data(){
    return {
      lan: "EN"
    }
  }
})