import vue from 'vue'
import { bus } from '../main';

import Home from './Sidebar/Home.vue'
import ServerIntro from './Sidebar/ServerIntro.vue'

export default vue.extend({
  name: 'Sidebar',
  components:{
    Home,
    ServerIntro
  },
  created(){
    bus.$on("ChangeLan", this.Lan);
    bus.$on("ChangeTopic", this.Topic);
  },
  destroyed(){
    bus.$off("ChangeLan", this.Lan);
    bus.$off("ChangeTopic", this.Topic);
  },
  methods: {
    Topic(v){
      this.topic = v;
    },
    Lan(tag){
      this.lan = tag
    },
    To(page, index){
      bus.$emit("ChangePage", page, index);
    }
  },
  data(){
    return {
      topic: "Home",
      lan: "EN"
    }
  }
})