/* eslint-disable no-unused-vars */
import vue from 'vue'
import { bus } from '../main';
/**
 * Home
 */
import HomeWelcome from './Main/HomeWelcome.vue'

/**
 * Client
 */
 
/**
 * Server
 */
import ServerIntro from './Main/ServerIntro.vue'
import ServerDocsCommand from './Main/ServerDocsCommand.vue'
import ServerDocsDatabase from './Main/ServerDocsDatabase.vue'
import ServerDocsNetwork from './Main/ServerDocsNetwork.vue'

export default vue.extend({
  name: 'Main',
  components:{
    HomeWelcome,
    ServerIntro,
    ServerDocsCommand,
    ServerDocsDatabase,
    ServerDocsNetwork
  },
  created(){
    bus.$on("ChangeLan", this.Lan);
    bus.$on("ChangePage", this.Page);
  },
  destroyed(){
    bus.$off("ChangeLan", this.Lan);
    bus.$off("ChangePage", this.Page);
  },
  methods: {
    Lan(tag){
      this.lan = tag;
    },
    Page(v, index){
      this.page = v;
    }
  },
  data(){
    return {
      lan: "EN",
      page: "Home-Welcome"
    }
  }
});